<template>
  <header 
    id="site-header"
    v-bind:class="[{ menuIsOpen: menuIsOpen }]">
    <h1 id="logo">
      <a href="//fractalcaravan.com">
        <img src="../assets/logo.png" alt="Fractal Caravan"/>
      </a>
    </h1>
    <nav>
      <a @click="handleClick('shows')"><h2>SHOWS</h2></a>
      <a @click="handleClick('videos')"><h2>VIDEOS</h2></a>
      <a @click="handleClick('pix')"><h2>PIX</h2></a>
      <a @click="handleClick('bio')"><h2>BIO</h2></a>
      <a @click="handleClick('music')"><h2>MUSIC</h2></a>
      <a target="_blank" href="//fractalcaravan.bandcamp.com"><h2>STORE</h2></a>
      <a @click="handleClick('contact')"><h2>CONTACT</h2></a>
    </nav>
    <div id="menu-button" @click="toggleMenu">
      <svg x="0px" y="0px" width="90px" height="90px" viewBox="0 0 90 90">
        <rect x="24" y="29" width="46" height="4"/>
        <rect x="24" y="44" width="46" height="4"/>
        <rect x="24" y="59" width="46" height="4"/>
      </svg>
    </div>
  </header>
</template>

<script>
export default {
  name: "Header",
  data() {
    return {
      menuIsOpen: false
    };
  },
  methods: {
    toggleMenu() {
      this.menuIsOpen = !this.menuIsOpen;
    },
    handleClick(target) {
      if (this.menuIsOpen) this.toggleMenu();
      setTimeout(() => {
        document.getElementById(target).scrollIntoView({
          behavior: "smooth",
          block: "start"
        });
      }, 1);
    }
  }
};
</script>

