<template>
  <section id="pix">
    <header>
      <h2>PIX</h2>
    </header>
    <main>
      <div id="featured">
        <img v-bind:src="featured">
      </div>
      <ol id="thumbnails">
        <li 
          v-for="(pic, index) in pix"
          :key="index">
          <img 
            class="thumbnail" 
            v-bind:src="pic.src"
            @click="setFeatured(index)">
        </li>
      </ol>
    </main>
  </section>
</template>

<script>
export default {
  name: "Pix",
  methods: {
    setFeatured(index) {
      this.featured = `pix/${index}.jpg`;
    }
  },
  data() {
    return {
      featured: "pix/0.jpg",
      pix: [
        { src: "pix/0.jpg" },
        { src: "pix/1.jpg" },
        { src: "pix/2.jpg" },
        { src: "pix/3.jpg" },
        { src: "pix/4.jpg" },
        { src: "pix/5.jpg" },
        { src: "pix/6.jpg" },
        { src: "pix/7.jpg" }
      ]
    };
  }
};
</script>

